// 引入我们的vue和vuex。
import Vue from 'vue'
import Vuex from 'vuex'
// 使用我们vuex，引入之后用Vue.use进行引用
Vue.use(Vuex)

const state = {
  count: 1,
  Num: 10
}

const getters = {
  // getCount: state => { state.count += 100; return state.count }
  getCount: state => state.count,
  testNum: state => state.Num
}

const mutations = {
  add (state) {
    state.count += 10
  },
  reduce (state) {
    state.count -= 1
    // state.count -= payload.nums
  }
}

const actions = {
  // addAction (context, n) {
  //   context.commit('add', n)
  // },
  addAction ({commit}) {
    return new Promise((resolve, reject) => {
      setTimeout(() => {
        commit('add')
        resolve()
      }, 3000)
    })
  },
  // reduceAction (context) {
  //   context.commit('reduce')
  // }
  // reduceAction ({commit}) {
  //   console.log({commit})
  //   commit('reduce')
  // }
  reduceAction ({dispatch, commit}) {
    return dispatch('addAction').then(() => {
      commit('reduce')
    })
  }
}
// store.dispatch()
export default new Vuex.Store({
  state, getters, mutations, actions
})



